.comment__input {
    width: 100%;
    display: flex;
    gap: 20px;
    margin: 2rem 0rem;
    margin-left: 2rem;
}

.comment__input__nameAndPassword form {
    display: flex;
    gap: 10px;
}

.comment__input__text__register {
    display: inline-flex;
}

#comment__nickname {
    width: 100px;
    padding: 3px 0;

}

#comment__password {
    width: 100px;
    padding: 3px 0;
}

#comment__text {
    width: 450px;
    padding: 3px 0;
}

.comment__input__text__register input {
    margin-left: 10px;
}

.comment__input__text__register button {
    position: static;
    margin-left: 10px;
}