@import url('./Globar.css');

.footerBar {
  width: 200px;
  height: 35px;
  box-shadow: inset 1px 1px 3px 1px rgb(123, 123, 123);
  margin-left: 6rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.footerBar:active {
  box-shadow: 1px 1px 3px 1px rgb(123, 123, 123);
}

.footerBar span {
  font-size: .8rem;
  margin-left: 7px;
}

.footerBar img {
  width: 25px;
  padding-left: 1rem;
}

.footerBar h5 {
  font-size: .8rem;
  margin-left: 7px;
  font-weight: normal;
}

.Minimize-tab {
  position: absolute;
  background: var(--secondary-color);
  box-shadow: inset 1px 1px 3px 1px rgb(123, 123, 123);
  border: none;
  padding: 6px 10px;
  cursor: pointer;
  left: 100px;
  top: 50%;
  transform: translateY(-50%);
  min-width: 200px;
  font-weight: 500;
  display: flex;
  align-items: center;
}

.Minimize-tab-off {
  box-shadow: 0px 0px 2px 1px rgb(123, 123, 123);
}

.Minimize-tab img {
  width: 15px;
  margin-right: 10px;
}

.Minimize-tab:active {
  /* 시작버튼 클릭시 버튼 들어가는 느낌주기, 나중에 js로 active 토글빼기*/
  box-shadow: inset -.3rem -.1rem 1.4rem rgb(123, 123, 123), inset .3rem .4rem .8rem rgb(123, 123, 123);
}