@import url('./Globar.css');

button {
    position: absolute;
    background: rgb(192, 192, 192);
    box-shadow: 1px 1px 1px 1px rgb(123, 123, 123);
    border: none;
    padding: 4px 10px;
    cursor: pointer;
}

button:active {
    /* 시작버튼 클릭시 버튼 들어가는 느낌주기, 나중에 js로 active 토글빼기*/
    box-shadow: inset -.3rem -.1rem 1.4rem rgb(123, 123, 123), inset .3rem .4rem .8rem rgb(123, 123, 123);
}

button div {
    display: flex;
    align-items: center;
    justify-content: center;
}

button img {
    width: 30px;
    height: 30px;
}

button span {
    font-size: 1rem;
}