.header {
    width: 100%;
    height: 25px;
    background: #020183;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 5;
}

.header h5 {
    padding-left: 1rem;
    font-size: 1.1rem;
}

.button-container {
    width: 25px;
    height: 100%;
}

.button-container button {
    font-weight: bold;
}